
/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

div.lead-art {
	margin-top: 1em;
}

.inline-media,
.simplePullQuote {
	float: right;
	margin-left: 16px !important;
	max-width: 620px;
	width: 100%;
}
.inline-media img {
	display: block;
}
.single .inline-media,
.single .simplePullQuote,
figure.alignright {
	float: right;
	margin-left: 16px !important;
	margin-right: -384px !important;
}
.simplePullQuote {
	box-shadow: none !important;
	border: 6px solid $accent !important;
	border-width: 10px 0 0 !important;
	background-image: none !important;
	font: 900 36px/1.5 $serif;
	margin: 0 0 0 16px !important;
	padding: 0 !important;
	text-indent: 0 !important;
	width: auto !important;
}
/* @todo: consolidate alignment with architecture alignment styles */

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: left;
}
figure.alignleft {
	margin-left: 0;
}
/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}
